import ListMixin from '@/mixin/List.js'
export default {
  name: 'scenicCollection',
  mixins: [ListMixin],
  data () {
    return {
		loaded: false
    }
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.userFavoriteScenicUrl, {
        page: this.page.num
      }, null, (data) => {
        this.loadSuccess(data.data)
      }, (errorCode, error) => {
        this.loadFail()
      }, () => {
		  this.loaded = true
		  uni.stopPullDownRefresh()
	  })
    },
    cancleFavorite (index) {
		let item = this.list[index]
		uni.showModal({
			title: '提示',
			content: '是否要取消收藏？',
			confirmText: '取消',
			cancelText: '再想想',
			success: (res) => {
				if (res.confirm) {
					this.$http(this.$urlPath.userUnFavoroteScenicUrl, {
					  scenic_id: item.s_id
					}, '正在取消', (data) => {
					  this.$toast('取消收藏成功')
					  this.list.splice(index, 1)
					}, (errorCode, error) => {
					  this.$toast(error)
					})
				}
			}
		})
    },
	startScenicInfo (index) {
		this.$push(this.$mRouter.scenicDetail({
			scenicId: this.list[index].s_id
		}))
	},
	onRefresh () {
		this.page.num = 1
		this.getData()
	}
  }
}
